
@fontColor:#64DFDE;

@media screen and (max-width: 319px) {
    html {

        font-size: 12px;

    }
}

@media screen and (min-width: 320px) and (max-width:359px) {
    html {

        font-size: 12.8px;

    }
}


@media screen and(min-width: 360px)and(max-width: 374px) {

    html {

        font-size: 14.4px;

    }
}

@media screen and(min-width: 375px)and(max-width: 385px) {

    html {

        font-size: 15px;

    }
}

@media screen and(min-width: 386px)and(max-width: 392px) {

    html {

        font-size: 15.44px;

    }
}

@media screen and(min-width: 393px)and(max-width: 400px) {

    html {
        font-size: 16px;
    }

}
@media screen and(width: 393px) {

    html {
        font-size: 16px;
    }
    .homePage-phone {
        main {
            margin-bottom: 40px;

            hgroup {
                padding-top: 10.2rem;

                .banner-p1 {
                    font-size: 2.6rem;
                    line-height: 4.14rem;
                    text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                }

                .banner-p2 {
                    font-size: .95rem;
                    line-height: 1.56rem;
                    text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.3);
                }

            }
        }

        .contact-us-txt {
            margin-top: 19%;

            p {

                font-size: 1.88rem;
                line-height: 2.58rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
            }

            .uploadBtn {
                width: 82vw;
                height: 22vw;

                .btn-icon {
                    width: 50px;
                    margin-left: 25px;
                    margin-right: 10px;
                }

                span {
                    font-size: 1.88rem;
                    line-height: 2.58rem;
                    text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                    margin-left: 18px;
                }
            }

            .btn1 {
                margin-top: 20px;

            }

            .btn2 {
                margin-top: 10px;
            }

        }
    }

}

@media screen and(min-width: 401px)and(max-width: 414px) {

    html {

        font-size: 16.48px;

    }

}